import React from 'react';

function Maps_argument_check(props) {

	return ( 
			<div className="form-check pt-2 pl-2 pb-2 pr-5">
				<input className="form-check-input" type="checkbox" value="" id={props.idScenario} />
				<label className="form-check-label" for="flexCheckDefault">
					{props.scenario}
				</label>
			</div>	
		);
}

export default Maps_argument_check;