import React from 'react';
import TeamArgument from '../team_argument/team_argument';

const TEAM_SETTINGS = [
  {
    title: 'Team 1',
    label: 'Team1',
    numbers: [1, 4, 2, 3, 6, 5, 8],
  },
  {
    title: 'Team 2',
    label: 'Team2',
    numbers: [2, 3, 1, 4, 5, 6, 7],
  }
];

const Team_structure = ({ teams, populations, onSelect }) => {
  const team1 = teams['Team1'];
  const team2 = teams['Team2'];
  return (
    <div className="col-md-4">
	    <div className="row">
		    <TeamArgument populations={populations} team={team1} onSelect={onSelect} setting={TEAM_SETTINGS[0]} />
		    <TeamArgument populations={populations} team={team2} onSelect={onSelect} setting={TEAM_SETTINGS[1]} />
	    </div>
    </div>
  );
};

export default Team_structure;
